export default {
	data() {
		return {
			name: '',
			token: ''
		}
	},
	onLoad: function() {
		this.token = uni.getStorageSync('token')
	},
	onShow: function() {},
	methods: {
		submit: function() {
			if (this.name == '') {
				this.$myToast('要修改新昵称的话不能为空哦(⊙o⊙)？')

			} else {
				var data = {
					token: this.token,
					username: this.name
				}
				this.$urlRequest('editUserInfo.do', data).then(res => {
					if (res.state == 'true') {
						this.$myToast('修改成功')
						setTimeout(function() {
							uni.switchTab({
								url: '/pages/mine/mine',
							})
						}, 1500);
					} else {
						this.$myToast(res.msg)
					}
				})
			}
		}
	}
}
